.editor {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background: #fff;
}
.editorHd {
  padding: 8px;
  border: 1px solid #e6e9ed;
}
.editorBd {
  flex: 1 1;
}
.editorSidebar,
.editorContent {
  display: flex;
  flex-direction: column;
}
.editorSidebar:first-child {
  border-right: 1px solid #e6e9ed;
}
.editorSidebar:last-child {
  border-left: 1px solid #e6e9ed;
}
.flow,
.mind,
.koni {
  flex: 1 1;
}
.lockIconWrapper {
  height: 50px;
  width: 50px;
}
.lockIcon {
  position: absolute;
  top: 8px;
  right: 8px;
  line-height: inherit;
  cursor: pointer;
}
.lockIcon:before {
  content: '';
  width: 0;
  height: 0;
  border-width: 25px;
  border-color: #e5e5e5 #e5e5e5 transparent transparent;
  border-style: solid;
  position: absolute;
  top: -8px;
  left: -42px;
  z-index: 1000;
}
.icon {
  position: absolute;
  top: -1px;
  right: -4px;
  font-size: 18px;
  z-index: 1003;
}
.unlock {
  color: #ababab;
  position: absolute;
  top: -1px;
  right: -4px;
  font-size: 18px;
  z-index: 1003;
}
.lock {
  color: #00f;
  position: absolute;
  top: -1px;
  right: -4px;
  font-size: 18px;
  z-index: 1003;
}
