.canvaHeader {
  background-color: #F2F2F2;
  border-bottom: 1px solid #cedae3;
}
.canvaHeader .canvaTitle {
  padding-top: 14px;
  padding-left: 20px;
  height: 50px;
  border-bottom: 1px solid #C9C9C9;
}
.canvaHeader .canvaTitle .taskflow {
  min-width: 80px;
  max-width: 200px;
  color: #283449 !important;
  text-align: left;
  font-size: 16px;
  font-weight: bold;
  line-height: 20px;
}
.canvaHeader .canvaTitle .taskflow i {
  font-size: 30px;
}
.canvaHeader .canvaTitle .operatorSpan {
  font-size: 14px;
  color: #9D9D9D;
  cursor: pointer;
}
.canvaHeader .canvaTitle .operatorSpan:hover {
  color: #1E54D6;
}
.canvaHeader .canvaTitle .divider {
  background-color: #9B9B9B;
  width: 1px;
  font-size: 16px;
  line-height: 16px;
}
.canvaHeader .canvaTitle .scheduleStatus {
  float: right;
  margin-right: 15px;
  margin-top: 8px;
}
.canvaHeader .toolbar {
  display: flex;
  position: relative;
  align-items: center;
  height: 38px;
}
.canvaHeader .toolbar .command i {
  display: inline-block;
  width: 20px;
  height: 27px;
  margin: 0 3px;
  padding-top: 6px;
  text-align: center;
  cursor: pointer;
}
.canvaHeader .toolbar .command i:hover {
  border: 1px solid #e6e9ed;
}
.canvaHeader .toolbar .disable i {
  color: rgba(0, 0, 0, 0.25);
  cursor: auto;
}
.canvaHeader .toolbar .disable i:hover {
  border: 1px solid #fff;
}
.canvaHeader .toolbar .com {
  margin: 0 11px;
  color: #777777;
  cursor: pointer;
}
.canvaHeader .toolbar .com i {
  margin: 0 4px;
  font-size: 20px;
}
.canvaHeader .toolbar .com:hover {
  color: #1E54D6;
}
.canvaHeader .toolbar .com:last-child {
  position: relative;
  float: right;
}
.canvaHeader .toolbar .dropdown {
  color: #777777;
  cursor: pointer;
}
.canvaHeader .toolbar .dropdown i {
  margin: 0 4px;
  font-size: 20px;
}
.canvaHeader .toolbar .dropdown .ant-menu .ant-menu-item a i {
  margin: 0 4px;
  font-size: 200px;
}
.canvaHeader .toolbar .divider {
  background-color: #D8D8D8;
  width: 1px;
  font-size: 20px;
  line-height: 22px;
}
.canvaHeader .tooltip .ant-tooltip-inner {
  font-size: 12px;
  border-radius: 0;
}
.canvaHeader .runningMode {
  float: right;
  margin-right: 15px;
  margin-top: -35px;
  font-size: 20px;
}
.modal .modalUl {
  margin-left: 20px;
  margin-top: 20px;
  padding-left: 0;
  height: 38px;
  border-bottom: 1px solid #D8D8D8;
}
.modal .modalUl .modalUlLi {
  display: inline-block;
  margin-right: 35px;
  font-size: 14px;
  padding: 5px 0;
  cursor: pointer;
}
.modal .modalUl .modalUlLi a {
  padding: 10px 0;
  color: inherit;
}
.modal .modalUl .modalUlLi a:focus {
  color: #1E54D6;
  border-bottom: 2px solid #1E54D6;
}
.modal .runningInstanceModalDiv {
  margin: 0 auto;
  min-height: 150px;
}
.modal .runningInstanceModalDiv .runningInstanceToolbar {
  text-align: right;
  height: 34px;
  font-size: 17px;
  color: #9ebedd;
}
.modal .runningInstanceModalDiv .runningInstanceContainer {
  text-align: center;
}
