.contentWrap {
  min-height: calc(100%);
  background-color: #ffffff;
  padding: 20px;
}
.contentWrap .buttonWrap {
  width: 100%;
  margin-bottom: 10px;
}
.contentWrap .buttonWrap button {
  margin-right: 10px;
}
.canvasContainer {
  padding: 5px;
  height: 100%;
  width: 100%;
  background-color: #fff;
}
.canvasContainer .pageheader {
  width: 100%;
  border: 1px solid #ebedf0;
}
.canvasContainer .editor {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background: #fff;
}
.canvasContainer .editorHd {
  padding: 8px;
  border: 1px solid #e6e9ed;
}
.canvasContainer .editorBd {
  flex: 1 1;
}
.canvasContainer .editorSidebar,
.canvasContainer .editorContent {
  display: flex;
  flex-direction: column;
}
.canvasContainer .editorSidebar:first-child {
  border-right: 1px solid #e6e9ed;
}
.canvasContainer .editorSidebar:last-child {
  border-left: 1px solid #e6e9ed;
}
.canvasContainer .flow,
.canvasContainer .mind,
.canvasContainer .koni {
  flex: 1 1;
}
.canvasContainer .lockIconWrapper {
  height: 50px;
  width: 50px;
}
.canvasContainer .lockIcon {
  position: absolute;
  top: 8px;
  right: 8px;
  line-height: inherit;
  cursor: pointer;
}
.canvasContainer .lockIcon:before {
  content: '';
  width: 0;
  height: 0;
  border-width: 25px;
  border-color: #e5e5e5 #e5e5e5 transparent transparent;
  border-style: solid;
  position: absolute;
  top: -8px;
  left: -42px;
  z-index: 1000;
}
.canvasContainer .icon {
  position: absolute;
  top: -1px;
  right: -4px;
  font-size: 18px;
  z-index: 1003;
}
.canvasContainer .unlock {
  color: #ababab;
  position: absolute;
  top: -1px;
  right: -4px;
  font-size: 18px;
  z-index: 1003;
}
.canvasContainer .lock {
  color: #00f;
  position: absolute;
  top: -1px;
  right: -4px;
  font-size: 18px;
  z-index: 1003;
}
