.itemPanel {
  flex: 1 1;
  background-color: #fff;
  height: 100%;
}
.itemPanel .ant-card {
  background: #fafafa;
}
.itemPanel .ant-card-body {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.itemPanel .ant-card-body > div {
  margin-bottom: 16px;
}
.flowPanelContainer .buttonWrapper {
  position: absolute;
  bottom: 0px;
  text-align: center;
  width: 100%;
  margin-bottom: 15px;
}
.flowPanelContainer .buttonWrapper button {
  display: inline;
  width: 80%;
}
.custom .ant-modal-content {
  height: 652px;
}
.ant-menu .nodeContain {
  margin-bottom: 10px;
  position: relative;
}
.ant-menu .nodeContain div:nth-child(1) {
  cursor: auto !important;
  margin: 6px 0 7px 60px;
  z-index: 2;
  position: relative;
  width: 194px;
  background: 0;
}
.ant-menu .nodeContain div:nth-child(1):hover + div {
  background: #1E54D6;
  color: #fff;
}
.ant-menu .nodeContain div:nth-child(1) img {
  z-index: 2;
  opacity: 1 !important;
  cursor: pointer;
}
.ant-menu .nodeContain div:nth-child(1) img:hover {
  content: url("../../../common/testblueeee.png") !important;
}
.ant-menu .nodeContain .spanContain {
  padding: 0;
  background: #fff;
  position: absolute;
  left: 74px;
  top: 1px;
  height: 30px;
  width: 130px;
  line-height: 32px;
  padding-left: 5px;
  z-index: 1;
}
.ant-menu .customNodeContain .itemCol div {
  cursor: auto !important;
  padding: 8px 0;
  background: #fafafa;
  padding-left: 16px;
}
.ant-menu .customNodeContain .name {
  line-height: 48px;
  text-align: right;
  cursor: auto !important;
  padding: 0 !important;
  background: #fafafa;
}
.ant-menu-submenu,
.ant-menu-submenu-inline {
  width: 99.5%;
}
.sidebarMenu {
  max-height: 93%;
  overflow-y: auto;
}
.ant-collapse {
  border: 0px;
  border-radius: 0px;
}
.ant-collapse > .ant-collapse-item > .ant-collapse-header {
  padding: 10px 30px 10px 48px;
}
.ant-collapse > .ant-collapse-item > .ant-collapse-header:hover {
  color: #1890ff;
}
.ant-collapse > .ant-collapse-item.ant-collapse-no-arrow > .ant-collapse-header {
  padding: 10px 30px 10px 48px;
}
.ant-collapse > .ant-collapse-item {
  border-bottom: 0px;
}
.ant-collapse-content {
  border-top: 0px;
  background: #FAFAFA;
}
.ant-collapse-content > .ant-collapse-content-box {
  padding: 11px 20px 1px 60px;
}
.ant-menu-submenu-title {
  font-weight: bold;
}
