.loginContainer {
  height: 100%;
  width: 100%;
  position: relative;
  background-image: url('../../common/loginBackground.jpg');
  background-size: 100% 100%;
}
#root {
  height: 100%;
}
.logoContainer {
  position: absolute;
  top: 70px;
  left: 68px;
}
.logo {
  width: 48px;
  display: inline-block;
  margin-top: -30px;
  margin-right: 9px;
}
.logoP {
  display: inline-block;
}
.logoP p {
  margin-bottom: 0;
  color: white;
  font-weight: 700;
}
.title {
  color: white;
  position: absolute;
  top: 33%;
  left: 8%;
}
.title .topTitle {
  margin-bottom: 0;
  font-size: 46px;
  font-weight: 500;
  letter-spacing: -1px;
}
.title .subTitle {
  margin-bottom: 0;
  font-size: 20px;
  letter-spacing: 1px;
}
.title .mainTitle {
  margin-bottom: 0;
  font-size: 52px;
  font-weight: 500;
  letter-spacing: 5px;
}
.login-form {
  position: absolute;
  top: 62%;
  left: 8%;
}
.login-form .login-form-forgot {
  float: right;
}
.login-form .login-form-button {
  width: 100%;
  background: #0195DF;
  border-radius: 40px;
  font-size: 15px;
  letter-spacing: -1px;
  margin-top: 15px;
}
.login-form .login-form-forgot {
  float: right;
  color: #0296E2;
}
.login-form input {
  background: #060C2C;
  border: 1px solid #7F8489;
  color: #fff;
  width: 290px;
  border-radius: 40px;
}
.login-form .ant-form-item {
  margin-bottom: 15px;
}
.login-form .loginIcon {
  color: #ffffff;
  font-size: 22px;
  border: 1px solid #7F8489;
  width: 40px;
  height: 40px;
  text-align: center;
  vertical-align: middle;
  padding-top: 6px;
  border-radius: 40px;
}
.login-form .ant-input-affix-wrapper .ant-input-prefix {
  left: 0 !important;
}
.ant-checkbox-wrapper + span,
.ant-checkbox + span {
  color: #A1A1A1;
}
.ant-input-affix-wrapper .ant-input:not(:first-child) {
  text-align: center;
}
.has-error .ant-input-affix-wrapper .ant-input:focus {
  border-color: #7F8489;
}
.has-error .ant-input-affix-wrapper .ant-input,
.has-error .ant-input-affix-wrapper .ant-input:hover {
  background-color: #060C2C;
  border: 1px solid #7F8489;
}
.foot {
  position: absolute;
  bottom: 5px;
  width: 100%;
}
.foot p {
  color: #BAC0DD;
  text-align: center;
  font-size: 15px;
}
