.templateWrap {
  min-height: calc(100%);
  background-color: #ffffff;
  padding-top: 10px;
  padding-left: 10px;
  padding-bottom: 0px;
  display: flex;
  flex-direction: column;
}
.templateWrap .templateContent {
  display: flex;
  flex-direction: row;
}
.templateWrap .templateContent .listWrap {
  width: 300px;
  border-right: 1px solid #e6e9ed;
}
.templateWrap .templateContent .listWrap .ant-tabs-bar {
  margin: 0;
}
.templateWrap .templateContent .listWrap .ant-list-pagination {
  margin-top: 10px;
}
.templateWrap .templateContent .ggeditorWrap {
  width: calc(100% - 220px);
  height: calc(100vh - 230px);
}
.ant-popover-inner-content p:hover {
  color: #1890ff;
}
.ant-col-12 .anticon {
  cursor: pointer;
  position: absolute;
  right: 3px;
  bottom: 35px;
  font-size: 16px;
  color: #999;
  transition: all 0.3s;
}
